<template>
  <v-container pa-1>
    <v-layout column>
      <v-progress-circular
        :rotate="-90"
        :size="100"
        :width="15"
        :value="percentComplete"
        color="primary"
      >sent {{ count }} {{ percentComplete }}%</v-progress-circular>
      <v-layout justify-center ma-4>
        <v-chip color text-color="black">
          <v-avatar>
            <v-icon>account_circle</v-icon>
          </v-avatar>
          last sent: {{ email }}
        </v-chip>
      </v-layout>
      <span v-html="compiledHtmlMessage"></span>

      <p>{{ delay }} secs</p>
      <v-form v-model="valid" v-on:submit.prevent="onSubmit">
        <v-flex xs12 sm6 d-flex>
          <v-select :items="delays" v-model="delay" box label="Send Interval(sec)"></v-select>
        </v-flex>
        <v-switch v-model="autoSMTP" :label="`Use auto-smtp: ${autoSMTP.toString()}`"></v-switch>
        <v-flex xs12 sm6 d-flex>
          <v-select
            :disabled="autoSMTP == false"
            :items="providers"
            v-model="provider"
            box
            label="Email Provider"
          ></v-select>
        </v-flex>
        <v-text-field
          v-model="smtpHost"
          :disabled="autoSMTP"
          box
          label="SMTP Host"
          type="text"
          required
        ></v-text-field>

        <v-textarea
          v-model="providerUserPass"
          box
          label="SMTP(user:password) one per line"
          required
        ></v-textarea>

        <v-switch v-model="useProxy" :label="`Use Proxy: ${useProxy.toString()}`"></v-switch>
        <v-text-field v-if="useProxy" v-model="proxy" box label="Proxy" type="text" required></v-text-field>
        <v-text-field
          v-model="senderName"
          box
          label="Sender's Name (defaults to company name if empty)"
          type="text"
          required
        ></v-text-field>
        <v-text-field
          v-model="senderEmail"
          box
          label="Sender's Email(Required for AWS or sendgrid only)"
          type="text"
          required
        ></v-text-field>
        <v-textarea v-model="pageURLs" box label="pageUrls" required></v-textarea>
        <v-textarea
          v-model="emailName"
          box
          label="Recipients(test@test.com,Tester) one per line"
          required
        ></v-textarea>

        <v-text-field v-model="subject" box label="Subject" type="text" required></v-text-field>
        <v-textarea v-model="htmlMessage" box label="HTML Message" required></v-textarea>

        <hr />
        <p>{{ attachmentOption }}</p>
        <v-radio-group v-model="attachmentOption" row>
          <v-radio label="No attachment" value="no-attachment"></v-radio>
          <v-radio label="Attach pdf" value="attach-pdf"></v-radio>
          <v-radio label="Attach from pdf source" value="attach-pdf-from-source"></v-radio>
        </v-radio-group>

        <!--ATTACH FROM FILE -->
        <div v-show="attachmentOption == 'attach-pdf'">
          <v-btn raised class="primary" @click="onPickFile">Add attachment</v-btn>

          <v-progress-linear :value="percentageUploaded" :active="Boolean(this.percentageUploaded)"></v-progress-linear>

          <v-chip
            v-if="uploadedFileName"
            class="ma-2"
            close
            @click="
              uploadedFileName = '';
              percentageUploaded = 0;
            "
          >
            {{ compiledUploadedFileName }}
            {{ uploadedFileSize / 1000 }}KB
          </v-chip>

          <input type="file" style="display: none" ref="fileInput" @change="onFilePicked" />
        </div>

        <!-- ATTACH FROM PDFSOURCE -->
        <div v-show="attachmentOption == 'attach-pdf-from-source'">
          <v-textarea v-model="pdfSource" box label="PDF Source" required></v-textarea>
        </div>
        <v-text-field
          v-show="attachmentOption != 'no-attachment'"
          v-model="uploadedFileName"
          box
          label="Rename File"
          type="text"
          required
        ></v-text-field>
      </v-form>
      <v-btn v-on:click="submitForm" small color="blue" dark>SEND</v-btn>
      <v-snackbar v-model="snackbar">
        {{ snackbarText }}
        <v-btn color="green" text @click="snackbar = false">Close</v-btn>
      </v-snackbar>
    </v-layout>
  </v-container>
</template>

<script>
import { db, fb } from "../../firebase";
import { VueEditor } from "vue2-editor";
import * as htmlToText from "html-to-text";

export default {
  components: {
    VueEditor
  },
  data() {
    return {
      useProxy: false,
      proxy: "user:pass@ip:port",
      pdfSource: "",
      attachmentOption: "no-attachment",
      upLoadedFile: null,
      uploadedFileName: "",
      uploadedFileSize: null,
      percentageUploaded: 0,
      snackbar: false,
      snackbarText: "",
      providers: [
        "Naver",
        "Gmail",
        "SES-EU-WEST-1",
        "SendGrid",
        "Mailgun",
        "Mailjet",
        "Mandrill",
        "SendPulse",
        "SendinBlue",
        "PostmarkApp",
        "Sparkpost",
        "Godaddy",
        "GodaddyAsia",
        "GodaddyEurope",
        "Tipimail"
      ],
      provider: "SendGrid",
      smtpHost: "host:port",
      autoSMTP: true,
      delays: ["1", "2", "3", "4", "5", "7", "10", "15", "20", "30"],
      delay: "3",
      count: 0,
      value: 0,
      valid: false,
      emailName: "",
      name: "",
      email: "",
      senderName: "",
      senderEmail: "",
      providerUserPass: "",
      providerUser: "",
      providerPass: "",
      subject: "",
      companyDomain: "",
      companyName: "",
      htmlMessage: `%name% <br /> %company_name% <br /> %company_domain% <br />%page_url%`,
      pageURLs: "https://google.com/%email%",
      pageURL: "",
      sendDisabled: false,
      percentComplete: 0
    };
  },

  computed: {
    emailSize: function() {
      return this.emailName.split("\n").length;
    },

    compiledSenderEmail() {
      return this.senderEmail
        .split("%company_name%")
        .join(this.companyName)
        .split("%company_domain%")
        .join(this.companyDomain);
    },

    compiledSenderName() {
      return this.senderName.split("%company_name%").join(this.companyName);
    },

    compiledSubject() {
      return this.subject
        .split("%name%")
        .join(this.name)
        .split("%company_name%")
        .join(this.companyName)
        .split("%company_domain%")
        .join(this.companyDomain);
    },

    compiledPageURL: function() {
      return this.pageURL
        .split("%name%")
        .join(this.name)
        .split("%email%")
        .join(this.email);
    },

    compiledHtmlMessage: function() {
      return this.htmlMessage
        .split("%name%")
        .join(this.name)
        .split("%company_name%")
        .join(this.companyName)
        .split("%company_domain%")
        .join(this.companyDomain)
        .split("%page_url%")
        .join(this.compiledPageURL)
        .split("%email%")
        .join(this.email);
    },

    compiledUploadedFileName: function() {
      return this.uploadedFileName
        .split("%name%")
        .join(this.name)
        .split("%company_name%")
        .join(this.companyName);
    },

    compiledPdfSource: function() {
      return this.pdfSource
        .split("%name%")
        .join(this.name)
        .split("%company_name%")
        .join(this.companyName)
        .split("%company_domain%")
        .join(this.companyDomain)
        .split("%page_url%")
        .join(this.compiledPageURL);
    }
  },

  created() {},

  firestore() {
    return {
      users: db.collection("users")
    };
  },

  methods: {
    onPickFile() {
      this.$refs.fileInput.click();
    },

    onFilePicked(event) {
      let files = event.target.files;
      let file = files[0];

      this.uploadedFileName = file.name;
      this.uploadedFileSize = file.size;
      let storagRef = fb.ref(`${this.uploadedFileName}`);
      this.percentageUploaded = 0;
      let task = storagRef.put(file);

      task.on(
        "state_changed",

        //Monitor progress
        snapshot => {
          this.percentageUploaded =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log(this.percentageUploaded);
        },

        // Error
        err => {
          console.log(err);
        },

        // Completed
        () => {
          this.snackbarText = "upload successful!";
          this.snackbar = true;
        }
      );
    },

    submitForm() {
      let emailNamelist = this.emailName.split("\n");
      let pageURLsList = this.pageURLs.split("\n");

      let providerUserPassList = this.providerUserPass.split("\n");
      console.log(this.delay);

      console.log(emailNamelist);
      console.log(emailNamelist.length);

      /**
       * loop though emails provided `email,name`
       */
      for (let i = 0; i < emailNamelist.length; i++) {
        // go round list of emails
        let provider = this.$rr(providerUserPassList);

        // go round list of page links.
        let pageURL = this.$rr(pageURLsList);

        let providerUser = provider.split(":")[0].trim();
        let providerPass = provider.split(":")[1].trim();

        setTimeout(async () => {
          this.email = emailNamelist[i].split(",")[0];
          this.count = i + 1;
          console.log(this.count);
          this.percentComplete = Math.ceil(
            (this.count / emailNamelist.length) * 100
          );
          console.log(this.email);
          this.providerUser = providerUser;
          this.providerPass = providerPass;
          this.pageURL = pageURL;
          console.log(providerUser, providerPass, pageURL);

          this.name = emailNamelist[i].split(",")[1];
          this.companyDomain = this.email.split("@")[1];
          let companyName = this.email.split("@")[1].split(".")[0];
          this.companyName =
            companyName.charAt(0).toUpperCase() + companyName.slice(1);
          try {
            await this.$firestore.users.add({
              senderName: this.compiledSenderName,
              senderEmail: this.compiledSenderEmail,
              email: this.email,
              htmlMessage: this.$spintax.unspin(this.compiledHtmlMessage),
              textMessage: htmlToText.fromString(
                this.$spintax.unspin(this.compiledHtmlMessage)
              ),
              subject: this.$spintax.unspin(this.compiledSubject),
              provider: this.provider,
              providerUser,
              providerPass,
              attachmentOption: this.attachmentOption,
              pdfSource: this.compiledPdfSource,
              renameFilename: this.compiledUploadedFileName,
              proxy: this.proxy,
              useProxy: this.useProxy,
              smtpHost: this.smtpHost,
              autoSMTP: this.autoSMTP
            });
          } catch (e) {
            console.log(e);
          }
        }, this.delay * 1000 * i);
      }
    }
  }
};
</script>
